'use strict';

/**
* WebAssembly routine to compute the sum of the absolute values of the real and imaginary components of a single-precision complex floating-point vector.
*
* @module @stdlib/blas-base-wasm-scasum
*
* @example
* import Complex64Array from '@stdlib/array-complex64';
* import scasum from '@stdlib/blas-base-wasm-scasum';
*
* // Define a strided array:
* var x = new Complex64Array( [ 2.0, 1.0, 3.0, 5.0, 4.0, 0.0, 1.0, 3.0 ] );
*
* // Perform operation:
* var sum = scasum.main( x.length, x, 1 );
* // returns 19.0
*
* @example
* import Complex64Array from '@stdlib/array-complex64';
* import scasum from '@stdlib/blas-base-wasm-scasum';
*
* // Define a strided array:
* var x = new Complex64Array( [ 2.0, 1.0, 3.0, 5.0, 4.0, 0.0, 1.0, 3.0 ] );
*
* // Perform operation:
* var sum = scasum.ndarray( x.length, x, 1, 0 );
* // returns 19.0
*
* @example
* import Memory from '@stdlib/wasm-memory';
* import oneTo from '@stdlib/array-one-to';
* import scasum from '@stdlib/blas-base-wasm-scasum';
*
* // Create a new memory instance with an initial size of 10 pages (640KiB) and a maximum size of 100 pages (6.4MiB):
* var mem = new Memory({
*     'initial': 10,
*     'maximum': 100
* });
*
* // Create a BLAS routine:
* var mod = new scasum.Module( mem );
* // returns <Module>
*
* // Initialize the routine:
* mod.initializeSync();
*
* // Define a vector data type:
* var dtype = 'complex64';
*
* // Specify a vector length:
* var N = 4;
*
* // Define a pointer (i.e., byte offset) for storing the input vector:
* var xptr = 0;
*
* // Write vector values to module memory:
* mod.write( xptr, oneTo( N, dtype ) );
*
* // Perform computation:
* var sum = mod.main( N, xptr, 1 );
* // returns 10.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import Module from './module.js';


// MAIN //

setReadOnly( main, 'Module', Module );


// EXPORTS //

export default main;

// exports: { "Module": "main.Module" }
